<template>
  <div id="app">
    <v-app>
      <v-main>
        <div style="position:absolute;display:flex; width:100%; flex-direction: column; height: 100%; font-size: 12px;" v-if="ready">

          <div class="d-flex" style="width:100%;   border-bottom:1px solid hsla(0,0%,100%,.12); height:39px">
            <v-btn small icon @click="Screenshot" class=" pa-4 rounded-0"
              style="height:39px; width:40px;text-align: center ; text-transform: capitalize; border-right:1px solid hsla(0,0%,100%,.12); font-weight: 400;">
              <v-icon size="22">mdi-camera</v-icon>
            </v-btn>
            <v-btn small v-if="!is_macro_running" @click="Test" class="  pa-4  rounded-0" text
              style="height:39px; text-transform: capitalize;  border-right:1px solid hsla(0,0%,100%,.12);font-weight: 400">
              <span class="mx-2">Test</span>
            </v-btn>

            <div class="d-flex" style="flex: 1; align-items: center; padding: 0 16px;"
              v-if="!is_logged_in && is_macro_running">
              <div style="font-size:12px">Logging in {{ character.nickname }}</div>
              <v-btn @click="StopMacro" class="ml-auto rounded" tile outlined icon x-small width="20">
                <v-icon size="20">mdi-stop</v-icon>
              </v-btn>
            </div>
            <template v-else>
              <template v-if="is_macro_running">
                <div class="d-flex" style="flex: 1; align-items: center; padding: 0 16px;">
                  <div style="font-size:12px">{{ selected_macro }}</div>
                  <v-btn @click="StopMacro" class="ml-auto rounded" tile outlined icon x-small width="20">
                    <v-icon size="20">mdi-stop</v-icon>
                  </v-btn>
                </div>
              </template>
              <template v-else>
                <v-btn small v-for="(shortcut, si) in shortcuts " :key="si" @click="RunMacro(si.type, si.name)"
                  class="pa-4 rounded-0" text
                  style="height:39px; text-transform: capitalize;  border-right:1px solid hsla(0,0%,100%,.12);font-weight: 400">
                  <span class="mx-2">{{ shortcut.name }}</span>
                </v-btn>
              </template>
            </template>

          </div>
          <v-slide-x-transition>
            <div v-if="!is_macro_running" style="overflow:auto;  border-bottom:1px solid hsla(0,0%,100%,.12);  display: flex;flex-direction: column;">
              <v-text-field tile solo label="Search Routine" prepend-inner-icon="mdi-magnify" elevation="0" clearable
                flat hide-details v-model="search_text" height="48" style="max-height: 48px"></v-text-field>
              <v-divider></v-divider>
              <div style="flex:1; overflow:auto">
                <template v-for="(routine, i) in filtered_routines">
                  <div :key="i">
                    <div class="w-100 py-2 px-4 routine-header">
                      <div class=" routine-header-text">{{ routine.category }}</div>
                    </div>
                    <v-list class="py-0" style="background: transparent !important;" color="white" dense>
                      <v-list-item class="bg-transparent" v-for="(r, ii) in routine.list" :key="ii"
                        @click="RunMacro(routine.category, r)" style="font-size:12px">{{ r }}</v-list-item>
                    </v-list>
                  </div>
                </template>
              </div>
            </div>
          </v-slide-x-transition>

          
          <div class="d-flex" style="width:100%; height:39px; align-items: center; font-size: 12px; ">
            <v-btn small text @click="CopyFlashHWND" class="  rounded-0"
              style="height:39px;text-transform: capitalize; text-align: center ;  ">
              Flash HWND : {{ flash_hwnd && parseInt(flash_hwnd) !== 0 ? flash_hwnd : 'acquiring hwnd' }}

              <v-icon size="16" class="ml-2" v-if="flash_hwnd && parseInt(flash_hwnd) !== 0">mdi-content-copy</v-icon>
            </v-btn>
            <div class="ml-auto d-flex mx-2" style="align-items: center ; ">
              <div class="d-flex" style="text-align: center ; ">{{ progress }} Routines done</div>
              <v-btn small icon @click="routines_completion_detail = !routines_completion_detail" class="mx-1">
                <v-icon>{{ routines_completion_detail ? "mdi-chevron-down" :"mdi-chevron-up" }}</v-icon>
              </v-btn>
            </div>


          </div>
          <v-slide-y-reverse-transition>
            <div v-if="routines_completion_detail" style=" border-top:1px solid hsla(0,0%,100%,.12)">
              <div class="character-routines">
                <h2 class="pa-3">Routines</h2>
                <template v-for="(r, i ) in character.routines">
                  <div :class="{ 'text--': character.done.includes(r) }" :key="i" class="pa-3 d-flex"
                   >
                    <v-icon small style="width:30px" color="blue lighten-1"> {{ character.done.includes(r) ?
              'mdi-check-circle-outline' : '' }}</v-icon>
                    <div class="mx-2">{{ r }}</div>
                  </div>

                </template>

              </div>

            </div>
          </v-slide-y-reverse-transition>

        </div>
      </v-main>
    </v-app>
  </div>
</template>
<script>
import "@/style.scss"
const params = new URL(window.location).searchParams
const procedure = async (data) => {
  try {
    await window.chrome.webview.postMessage(data)
  }
  catch (ex) {
    console.log(ex)
  }
}
const routines = [
  {
    category: "Simple Task",
    list: [
      "Spin Coin",
      "Synth Gems",
      "Opening Pack"
    ]
  },
  {
    category: "Dailies",
    list: [
      "Corruption",
      "Ladder",
      "Bath",
    ]
  },
  {
    category: "Grind Gold",
    list: [
      "310", "320", "340", "355"]
  },
  {
    category: "Dungeons",
    list: [
      "30",
      "45",
      "55",
      "75",
      "85",
      "110",
      "125",
      "165",
      "175",
      "195",
      "215",
      "235",
      "255",
      "275",
      "295",
      "315",
      "335",
      "355",
      "375",
      "395",
      "415",
      "Pixie"
    ]
  }
]


export default {
  data: (vm) => ({
    ready: false,
    character: {
      nickname: "",
      routines: [],
      done: []
    },
    is_macro_running: true,
    is_logged_in: false,
    routines_completion_detail: false,
    routines,
    search_text: "",
    shortcuts: [
      { type: "dungeons", name: "D85" }
    ],
    selected_macro: 'Automate',
    workspace_mode: params.get("workspace_mode") || 'freemode',
    flash_hwnd: params.get("flash_hwnd") || 0,
  }),
  watch: {
    routines_completion_detail(n) {
      if(this.is_macro_running && n == true){
          procedure({ Event: "extend_script_panel"})
      }else if(n == false && this.is_macro_running){
            procedure({ Event: "extend_script_panel"}) 
      }   
    }
  },
  computed: {
    filtered_routines: function () {
      console.log(this.routines)
      let r = this.routines.map((r) => ({
        category: r.category,
        list: r.list.filter(l => l.toLowerCase().includes(this.search_text))
      })).filter((r) => r.list.length)
      console.log(r)
      return r
    },
    progress() {
      return `${this.character.done.length} / ${this.character.routines.length}`
    }
  },
  
  methods: {
    SetCharacter(character) {
      this.character = { ...this.character, ...character}
      console.log("SETTING CHARACTER", this.character)
      this.ready = true
    },
    CopyFlashHWND() {
      navigator.clipboard.writeText(this.flash_hwnd)
    },
    Screenshot() {
      procedure({ Event: "screenshot", Data: null })
    },
    Automate() {
      procedure({ Event: "run_macro", Data: `..\\automate` })
      this.selected_macro = "Automate"
      this.is_macro_running = true
    },
    Test() {
      procedure({ Event: "test", Data: null })
      this.selected_macro = "test"
      this.is_macro_running = true
    },
    RunMacro(category, routine) {
      console.log(category, routine)
      this.is_macro_running = true
      this.selected_macro = routine
      procedure({ Event: "run_macro", Data: encodeURI(`${category}/${routine}`) })
    },
    StopMacro() {
      this.is_macro_running = false
      this.selected_macro = ""
      this.is_logged_in = true
      procedure({ Event: "stop_macro", Data: null })
    }
  },
  mounted() {
    window.$com = this
    if (this.workspace_mode == 'freemode') {
      this.shortcuts.splice(0, 0, { type: "automate", name: "Automate" })
    }
  }

}
</script>